<template>
  <div class="hello" style="background-color: rgb(255, 204, 255)">
    <v-col cols="12" sm="12" md="12">
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title>
            <div class="text-h4 mb-1">TransReadFinder</div>
          </v-card-title>
          <v-card-text>
            <v-file-input
              multiple
              hide-details
              accept=".json"
              @change="loadExperimentFiles"
            />
          </v-card-text>
        </v-card>
      </v-row>
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title> Analysed projects </v-card-title>
          <v-card-text>
            <v-data-table
              dense
              dark
              style="margin: 10px"
              :headers="headers"
              :items="experiment_information"
              :items-per-page="-1"
              :sort-by="['score']"
              :sort-desc="[true]"
              class="elevation-1"
              @click:row="onFragmentRowClicked"
            >
              <template v-slot:[`item.link`]="{ value }">
                <a :href="value" target="blank">
                  {{ value }}
                </a>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-row>
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title> Candidates of all projects </v-card-title>
          <v-card-text>
          <v-data-table
        dense
        dark
        style="margin: 10px"
        :headers2="headers2"
        :items="candidate_information"
        :items-per-page="-1"
        :sort-by="['score']"
        :sort-desc="[true]"
        class="elevation-1"
        
      >
      </v-data-table>
          </v-card-text>
        </v-card>
      </v-row>
    </v-col>
  </div>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      headers2:[],
      experiment_information: [],
      candidate_information: [],
    };
  },
  components: {},
  mounted() {
    this.headers = [
      {
        text: "Project",
        value: "projectID",
      },
      { text: "Number of Experiments", value: "no_of_experiments" },
      {
        text: "Title",
        align: "start",
        sortable: true,
        value: "title",
      },
      {
        text: "Organism",
        sortable: true,
        value: "organism",
      },
      {
        text: "Disease",
        sortable: true,
        value: "disease",
      },
      {
        text: "Tissue",
        sortable: true,
        value: "tissue",
      },
      {
        text: "Instrument",
        sortable: true,
        value: "instrument",
      },
      {
        text: "Affiliation",
        value: "university",
      },
      {
        text: "Link",
        value: "link",
      },
      {
        text: "Number of Candidates",
        value: "no_of_candidates",
      },
      {
        text: "LDHB",
        value: "ldhb_found",
      },
      {
        text: "MDH1",
        value: "mdh1_found",
      },
      {
        text: "TGA [%]",
        value: "percentage_of_tga",
      },
    ];
    this.headers2 = [
      {
        text: "Number of projects",
        value: "proteinid",
      },
      {
        text: "Gene name",
        value: "genename",
      },
      // { text: "Detailed Information", value: "detailedInformation" },
      {
        text: "Score",
        align: "start",
        sortable: true,
        value: "score",
      },
      {
        text: "Sequence coverage [%]",
        sortable: true,
        value: "sequence_coverage",
      },
      {
        text: "Offtargets",
        sortable: true,
        value: "offtargets",
      },
      {
        text: "Fragment",
        value: "peptide",
      },
      {
        text: "Stopcodon",
        value: "stopcodon",
      },
      {
        text: "Downstream",
        value: "downstream",
      },
    ];
     this.updateExperimentInfo();
  },
  methods: {
    updateExperimentInfo() {
      let project_information = this.$store.state.data;
      this.experiment_information = [];
      this.candidate_information = [];

      for (const projectID in project_information) {
        const project = project_information[projectID];
        const fragment_information = project.fragment_information;
        const no_of_candidates = Object.keys(fragment_information).length;
        this.experiment_information.push({
          projectID: projectID,
          title: project.title,
          organism: project.organism,
          disease: project.disease,
          tissue: project.tissue
          .replace("[", "")
          .replace("]", "")
          .replaceAll("'", ""),
          instrument: project.instrument,
          university: project.university,
          link: project.link,
          no_of_candidates: no_of_candidates,
          ldhb_found: project.ldhb_found,
          mdh1_found:  project.mdh1_found,
          percentage_of_tga: project.percentage_of_tga,
          no_of_experiments: project.no_of_experiments,
        });
        //console.log('blubi', fragment_information.score);
      }
    },
    loadExperimentFiles(e) {
      const files = [...e];
      for (let i = 0; i < files.length; i++) {
        const reader = new FileReader();
        reader.onload = (result) => {
          const data = JSON.parse(result.target.result);
          this.$store
            .dispatch("processProject", data)
            .finally(() => this.updateExperimentInfo());
        };
        reader.readAsText(files[i]);
      }
    },
    onFragmentRowClicked(item) {
      this.$router.push("/experiment/" + item.projectID);
    },
  },
};
</script>
